<h2 id="node安装" tabindex="-1"><a class="header-anchor" href="#node安装" aria-hidden="true">#</a> node安装</h2>
<h3 id="下载node" tabindex="-1"><a class="header-anchor" href="#下载node" aria-hidden="true">#</a> 下载node</h3>
<p><a href="https://www.aliyundrive.com/s/2E9D8FW2mWq" target="_blank">点击下载</a></p>
<p>点击上方链接，下载node，或者你也可以到官方网站进行下载</p>
<p><a href="https://nodejs.org/en/download/" target="_blank" rel="noopener noreferrer">Download | Node.js (nodejs.org)<ExternalLinkIcon/></a></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013112000619" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013112000619.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>当你下载完成之后，你会得到如下文件</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013112130043" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013112130043.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<h3 id="安装过程化" tabindex="-1"><a class="header-anchor" href="#安装过程化" aria-hidden="true">#</a> 安装过程化</h3>
<ol>
<li>
<p>双击点击安装</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013112222351" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013112222351.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
<li>
<p>选择一个安装目录</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013132648644" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013132648644.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>这里的安装目录可以随便选择，<code>但是一定要记得此路径，在后面需要使用到</code></p>
</li>
<li>
<p>点击next</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128222436169" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128222436169.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<div class="custom-container warning"><p class="custom-container-title">WARNING</p>
<p>这里一定要选择<code>Add to PATH</code>，一定要记得选择<code>Add to PATH</code>，一定要记得选择<code>Add to PATH</code></p>
</div>
<p>这里还是点击next</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013132911956" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013132911956.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013132930873" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013132930873.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013133002992" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013133002992.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
</ol>
<p>​		然后等待安装完成之后，就可以了</p>
<h3 id="设置环境和变量" tabindex="-1"><a class="header-anchor" href="#设置环境和变量" aria-hidden="true">#</a> 设置环境和变量</h3>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>因为之前选择了<code>App to PATH</code>，所以我们值需要简单设置一下就行</p>
</div>
<h4 id="验证" tabindex="-1"><a class="header-anchor" href="#验证" aria-hidden="true">#</a> 验证</h4>
<p>上一步安装完成之后，按住<code>win r</code>键，输入<code>cmd</code></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013133147151" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013133147151.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>在面板中，输入<code>node -v</code>验证是否安装成功</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013133235630" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013133235630.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>当出现<code>v14.17.3</code>时，表示node已经安装成功了，此值也有可能不同，取决于你下的node版本</p>
<h4 id="设置" tabindex="-1"><a class="header-anchor" href="#设置" aria-hidden="true">#</a> 设置</h4>
<ol>
<li>
<p>进入上一步安装node的目录中，你将看到如下文件</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013133600368" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013133600368.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
<li>
<p>在此目录中，新建<code>node_cache</code>和<code>node_global</code>两个文件夹</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013133711581" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013133711581.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
<li>
<p>在<code>cmd</code>面板中，运行下面两个命令，一个一个的运行</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> config <span class="token builtin class-name">set</span> prefix <span class="token string">"D:\Develop<span class="token entity" title="\n">\n</span>odejs<span class="token entity" title="\n">\n</span>ode_global"</span>
<span class="token function">npm</span> config <span class="token builtin class-name">set</span> cache <span class="token string">"D:\Develop<span class="token entity" title="\n">\n</span>odejs<span class="token entity" title="\n">\n</span>ode_cache"</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>将后面的路径改成你自己对应文件夹就行</p>
</div>
</li>
<li>
<p>然后再运行下面这个命令</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> config <span class="token builtin class-name">set</span> <span class="token assign-left variable">registry</span><span class="token operator">=</span>http://registry.npm.taobao.org/ 
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>使用淘宝镜像，不然下载速度非常慢</p>
</li>
<li>
<p>设置环境变量</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013134927339" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013134927339.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013134955551" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013134955551.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211013135021613" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211013135021613.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
</ol>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128225456194" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128225456194.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128225939601" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128225939601.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>设置好之后，点击确定就行了</p>
<h3 id="验证-1" tabindex="-1"><a class="header-anchor" href="#验证-1" aria-hidden="true">#</a> 验证</h3>
<p>现在验证一下，刚才安装的是否正确</p>
<p>打开cmd面板，运行下面命令</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> root-g
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128230349713" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128230349713.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>如果输入命令之后，输出的<code>node_modules</code>前面的路径就是你刚才创建的<code>node_global</code>的路径，那么就表示你设置成功</p>
<h3 id="验证脚手架" tabindex="-1"><a class="header-anchor" href="#验证脚手架" aria-hidden="true">#</a> 验证脚手架</h3>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>一定要先验证<code>aurora</code>命令是否可用，否则安装主题会有点麻烦</p>
</div>
<p>在<code>cmd</code>中输入下面命令安装Aurora脚手架</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">npm</span> i vuepress-theme-cli -g
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>待下载成功之后，在控制台中输入<code>aurora blog</code>，然后按回车</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128231157929" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128231157929.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>如果提示下图</p>
<div class="custom-container warning"><p class="custom-container-title">WARNING</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token string">'aurora'</span> 不是内部或外部命令，也不是可运行的程序
或批处理文件。
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div></div>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128231224969" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128231224969.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>进入到环境变量中，新建一个，输入<a href="#%E8%AE%BE%E7%BD%AE">创建的node_global路径</a></p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128231434520" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128231434520.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p>正常情况，你输入<code>aurora blog</code>之后，便会下载一个模板</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211128231837831" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211128231837831.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</div>
<p>然后现在你可以了</p>
<p>根据面板中的指示，运行相应命令，就可以启动博客了</p>
